
import BScroll from 'better-scroll'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: 'goodsManage',
components: {
LoadMore
},
data () {
return {
tabIndex: 1,
params: {
merchantId: '',
status: 1, // 商品状态【1-已上架 2-未上架】
page: 1,
pageSize: 10
},
isMoreData: true,
data: [],
baseInfo: {
goodsDownNum: 0,
goodsOnNum: 0
},
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none'
}
},
methods: {
edit (item) {
console.log(this.tabIndex)
this.$store.state.tabIndex = this.tabIndex
this.$router.push({
name: 'goodsAdd',
query: {
id: item.id
}
})
event.stopPropagation()
},
getOff(item,status) {
this.$vux.loading.show({
text: '加载中...'
})
let txt = ''
if(status == 2) {
txt = '下架成功'
}
if( status == 1) {
txt = '上架成功'
}
this.$http.fetchGet('/merchant/good/update/status', {goodId: item.id, operate: status}).then((res) => {
this.$vux.loading.hide()
if(res.data.code == 200) {
this.$vux.toast.show({
text: txt,
position: 'middle'
})
this.refreshData()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
event.stopPropagation()
},
deleteList(item) {
this.$vux.confirm.show({
title : '此操作将永久删除，是否继续？',
onCancel : () => {

},
onConfirm : () => {
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/good/delete/goods?goodsId='+item.id).then((res)=>{
this.$vux.loading.hide()
if(res.data.code == 200) {
this.$vux.toast.show({
text: '删除成功',
position: 'middle'
})
this.refreshData()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
})
event.stopPropagation()
},
addGoods () {
this.$store.state.tabIndex = this.tabIndex
this.$router.push({
name: 'goodsAdd'
})
},
goDetail (id,dataStatus,dataRemark) {
this.$store.state.tabIndex = this.tabIndex
this.$router.push({
name: 'goodsDetail',
query: {
tabIndex: this.params.status,
id: id,
dataStatus: dataStatus,
dataRemark: dataRemark
}
})
},
getBack () {
this.$router.go(-1)
},
getData () {
this.$http.fetchGet('/merchant/good/get/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo.goodsDownNum = res.data.data.goodsDownNum
this.baseInfo.goodsOnNum = res.data.data.goodsOnNum
this.data = res.data.data.goodsList
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
this.initScroll()
})
},
refreshData () {
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。'
this.params.page = 1
this.$http.fetchGet('/merchant/good/get/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo.goodsDownNum = res.data.data.goodsDownNum
this.baseInfo.goodsOnNum = res.data.data.goodsOnNum
this.data = res.data.data.goodsList
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多'

this.scroll.refresh()
})
},
loadMoreData () {
if (this.data.length < this.params.pageSize || this.data.length < this.params.pageSize * this.params.page) {
this.isMoreData = false
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.params.page++
this.$http.fetchGet('/merchant/good/get/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo.goodsDownNum = res.data.data.goodsDownNum
this.baseInfo.goodsOnNum = res.data.data.goodsOnNum
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if (res.data.data.goodsList.length > 0) {
res.data.data.goodsList.map((item) => {
this.data.push(item)
})
} else {
this.isMoreData = false
}

this.pulldownMsg = '下拉刷新'

this.scroll.refresh()
})
},
initScroll () {
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper, { // 初始化better-scroll
probeType: 1, // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true 
})

this.scroll.on('scroll', (pos) => {
if (pos.y > 30) {
this.pulldownMsg = '释放立即刷新'
}
})

this.scroll.on('touchEnd', (pos) => { // 上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) { // 下拉加载
this.loadMoreData()
}
})
})
},
changeTab (tabIndex) {
this.tabIndex = tabIndex
this.$vux.loading.show({
text: '加载中...'
})
this.$refs.content.style.height = 'auto'
this.params.page = 1
this.params.status = tabIndex
this.refreshData()
},
refreshalert () { // 刷新成功提示
this.alertHook = 'block'
setTimeout(() => {
this.alertHook = 'none'
}, 1000)
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.params.merchantId = this.$store.state.merchantId
if(this.$store.state.tabIndex) {
this.tabIndex = this.$store.state.tabIndex
this.params.status = this.$store.state.tabIndex
this.$store.state.tabIndex = ''
}
this.getData()
}
}



